  line-height: 1em;
}

.error-message {
  background-color: #D9534F;
  border-radius: 10px;
  font-weight: light;
  color: #fff;
  padding: 15px;
}

.success-message {
  background-color: #5CB85C;
  border-radius: 10px;
  font-weight: light;
  color: #fff;
  padding: 15px;
}

.error-exit-message {
  color: grey;
}

.spacer {
  margin: 3% 0;
}

.facebook {
  width: 100%;
  background-color: #3B5999;
  height: 50px;
  margin: 1%;
  padding: 10px;
  border-radius: 5px;
  color: white;
}

.twitter {
  width: 100%;
  background-color: #55ACEF;
  height: 50px;
  margin: 1%;
  padding: 10px;
  border-radius: 5px;
  color: white;
}

.customised_alert {
  padding: 8px 20%;
}

.btn-large {
  text-transform: capitalize;
}

.social {
  border: 0;
  box-shadow: none;
}

.pic {
  width: 9%;
}

.loader {
  display: none;
  position: fixed;
  margin: 25% 50%;
}

.textarea-wrapper {
  color: rgba(4, 4, 4, 0.35);
  position: absolute;
  width: 90%;
  overflow: hidden;
  cursor: text;
  margin: 0 0 0 20px;
  padding-left: 10px;
  border-radius: 4px;
  height: 70px;
  overflow: hidden;
}

.bubble-content {
  position: relative;
  float: left;
  margin-left: 12px;
  width: 100%;
  padding: 0px 20px;
  border-radius: 10px;
  background-color: #FFFFFF;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}

.bubble {
  margin-top: 20px;
}

.point {
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 12px solid #FFF;
  position: absolute;
  left: -10px;
  top: 12px;
}

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: ".";
  clear: both;
  height: 0;
}

.clearfix {
  display: inline-block;
}

* html .clearfix {
  height: 1%;
}

